import { axiosApi } from "@/config/axios"

export const state = {
    abouthostel: []
}
export const getters = {
    get_abouthostel: (state) => state.abouthostel
}
export const mutations = {
    set_abouthostel: (state, data) => state.abouthostel = data
}
export const actions = {
    async fetchAbouthostel({commit}){
        try{
            var response = await axiosApi.get("abouthostelapi")
            console.warn(response.data)
            if(response.status==200){
                commit('set_abouthostel',response.data.data)
            }
        }
        catch(e){
            console.warn(e)
        }
    }
}