<template>
  <div>
    <vue-flux
      :options="fluxOptions"
      :images="fluxImages"
      :transitions="fluxTransitions"
      ref="slider"
    >
      <flux-pagination slot="pagination"></flux-pagination>
    </vue-flux>
  </div>
</template>

<script>
import { VueFlux, FluxPagination, Transitions } from "vue-flux";
import { mapGetters } from "vuex";
export default {
  name: "CarouselComponent",
  components: {
    VueFlux,
    FluxPagination,
  },
  computed: {
    ...mapGetters({
      slide: "get_slide",
    }),
    fluxImages() {
      return this.slide.map((m) => m.image);
    },
  },
  data: () => ({
    fluxOptions: {
      autoplay: true,
    },

    fluxTransitions: {
      transitionBook: Transitions.transitionWaterfall,
    },
  }),
};
</script>

<style scoped>
.carousel {
  width: 100%;
  height: 600px;
}

.carousel .carousel-item {
  width: 100%;
  height: 600px;
}

.carousel-item img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}
</style>
