import { axiosApi } from "@/config/axios"

export const state = {
    service: []
}
export const getters = {
    get_service: (state)=>state.service
}
export const mutations = {
    set_service: (state,data)=>state.service = data
}
export const actions = {
    async fetchService({commit}){
        try{
            var response = await axiosApi.get("serviceapi")
            console.warn(response.data.data)
            if(response.status==200){
                commit('set_service',response.data.data)
            }
        }
        catch(e){
            console.warn(e)
        }
    }
}