import { axiosApi } from "@/config/axios"

export const state = {
    welcome: []
}
export const getters = {
    get_welcome: (state) => state.welcome
}
export const mutations = {
    set_welcome: (state, data) => state.welcome = data
}
export const actions = {
    async fetchWelcome({commit}){
        try{
            var response = await axiosApi.get("welcomeapi")
            console.warn(response.data)
            if(response.status==200){
                commit('set_welcome',response.data.data)
            }
        }
        catch(e){
            console.warn(e)
        }
    }
}