import { axiosApi } from "@/config/axios"

export const state = {
    rule: {}
}
export const getters = {
    get_rule: (state) => state.rule
}
export const mutations = {
    set_rule: (state, data) => state.rule = data
}
export const actions = {
    async fetchRule({commit}){
        try{
            var response = await axiosApi.get("ruleapi")
            console.warn(response.data)
            if(response.status==200){
                commit('set_rule',response.data)
            }
        }
        catch(e){
            console.warn(e)
        }
    }
}