<template>
  <div id="app">
    <!-- <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </nav> -->
    <!-- <jawn></jawn> -->
    <router-view />
  </div>
</template>
<script>
import { mapActions } from "vuex";
// import Jawn from "vue-loading-spinner"

export default {
  name: "App",
  // components: {Jawn},
  methods: {
    ...mapActions([
      "fetchSlide",
      "fetchAbout",
      "fetchService",
      "fetchCompany",
      "fetchGallery",
      "fetchMeal",
      "fetchRoom",
      "fetchRule",
      "fetchWelcome",
      "fetchRoomgallery",
      "fetchAbouthostel",
      "fetchFeedback",
    ]),
  },
  created() {
    this.fetchSlide();
    this.fetchAbout();
    this.fetchService();
    this.fetchCompany();
    this.fetchGallery();
    this.fetchMeal();
    this.fetchRoom();
    this.fetchRule();
    this.fetchWelcome();
    this.fetchRoomgallery();
    this.fetchAbouthostel();
    this.fetchFeedback();
  },
};
</script>


<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Times New Roman", Times, serif;
}
nav {
  font-family: Georgia, "Times New Roman", Times, serif;
  padding: 30px;
}

.navbar-nav li {
  height: 100%;
  font-size: 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-shadow: 0 0 15px #c7d2e0;
  transition: all 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
  text-align: center;
  padding: 0 10px;
  margin-right: 0px;
}

.navbar-nav li:after,
.navbar-nav li:before {
  content: "";
  position: absolute;
  display: block;
  border: 0px solid transparent;
}

.navbar-nav li:after {
  width: 0%;
  height: 80%;
  border-top: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transition: all 0.3s ease;
}

.navbar-nav li:before {
  width: 120%;
  height: 0%;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  transition: all 0.5s ease;
}

.navbar-nav li:hover::before {
  height: 80%;
}

.navbar-nav li:hover::after {
  width: 120%;
}

nav a {
  font-weight: bold;
  color: #2c3e50;
}

nav a.router-link-exact-active {
  color: #42b983;
}

.home {
  width: 80%;
  margin: auto;
  text-align: center;
  padding-top: 50px;
}
.home-col {
  flex-basis: 32%;
  border-radius: 10px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.home-col img {
  width: 100%;
  display: block;
}
.layer {
  background: transparent;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s;
}
.layer:hover {
  background: #3390ad;
}
.layer h3 {
  width: 100%;
  font-weight: 500;
  color: azure;
  font-size: 26px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  opacity: 0;
  transition: 0.5s;
}
.layer:hover h3 {
  bottom: 49%;
  opacity: 1;
}

.login-form {
  margin-top: 60px; /* adjust this value to match your navbar height */
  margin-bottom: 60px; /* adjust this value to match your footer height */
}
</style>

