import { axiosApi } from "@/config/axios"

export const state = {
    roomgallery: []
}
export const getters = {
    get_roomgallery: (state)=>state.roomgallery
}
export const mutations = {
    set_roomgallery: (state,data)=>state.roomgallery = data
}
export const actions = {
    async fetchRoomgallery({commit}){
        try{
            var response = await axiosApi.get("roomgalleryapi")
            console.warn(response.data.data)
            if(response.status==200){
                commit('set_roomgallery',response.data.data)
            }
        }
        catch(e){
            console.warn(e)
        }
    }
}