import { axiosApi } from "@/config/axios";

export const state = {
  user: {},
  message: "",
};
export const getters = {
  get_user: (state) => state.user,
  get_message: (state) => state.message,
};
export const mutations = {
  set_user: (state, payload) => (state.user = payload),
  set_message: (state, payload) => (state.message = payload),
};
export const actions = {
  async updateUser({ commit }, data) {
    var user = JSON.parse(localStorage.getItem("user"));
    console.warn("alisha token");
    console.warn(user);
    if (user != null) {
      var token = user.accessToken;
      console.warn(token);
      axiosApi.defaults.headers.common["Authorization"] = `Bearer ${token}`;
    }
    var response = await axiosApi.put(`update-user`, data);
    if (response.status == 200) {
      commit("set_user", response.data);
      alert("User updated successfully");
    }
  },

  async changePassword({ commit }, data) {
    var user = JSON.parse(localStorage.getItem("user"));
    console.warn("alisha token");
    console.warn(data);

    console.warn(user);
    if (user != null) {
      var token = user.accessToken;
      console.warn(token);
      axiosApi.defaults.headers.common["Authorization"] = `Bearer ${token}`;
    }
    var response = await axiosApi.put(`change-password`, data);
    if (response.status == 200) {
      commit("set_message", response.data);
      alert("Password changed successfully");
    }
  },
};
